<template>
  <transition-group class="grid" name="list" v-if="items.length">
    <section class="card shadow" v-for="item in items" :key="item.id">
      <router-link class='card__link' :to="{ name: 'view', params: { id: item.id } }">
        <div class="card__cover">
          <img class="card__image" :src="item.cover" alt="">
        </div>

        <article class="card__content">
          <h3 class="card__title">{{ item.title }}</h3>
          <c-tags class="card__tags" :items="item.tags" limit="3" no-route />
          <p class="card__text" v-text="item.text.slice(0, 200).replace(/<[^>]+>/g,'')"></p>
        </article>
      </router-link>

      <footer class="card__footer">
        <router-link
          class="card__date"
          :to="{ name: 'view', params: { id: item.id } }"
          tag="time"
        >
          {{ utils.formatDate(item.date) }}
        </router-link>
        <c-like class="card__like" :id="item.id" />
      </footer>
    </section>
  </transition-group>

  <div class="not-result" v-else>Ничего не найдено</div>
</template>

<script>
import utils from '@/utils'

export default {
  name: 'c-grid',

  components: {
    cTags: () => import('@/components/Tags'),
    cLike: () => import('@/components/Like')
  },

  props: {
    items: {
      type: Array,
      required: true
    }
  },

  data: () => ({
    utils
  })
}
</script>

<style scoped>
  .grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
  }

  .card {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #fff;
    border-radius: 3px;
    overflow: hidden;
    width: 100%;
  }
  .card__link {
    text-decoration: none;
  }
  .card__cover {
    position: relative;
    padding-bottom: 75%;
    height: 0;
    width: 100%;
    overflow: hidden;
  }
  .card__cover::before {
    content: '';
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: background .3s;
  }
  .card:hover .card__cover::before {
    background: rgba(0,0,0,.3);
  }
  .card__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform .3s;
  }
  .card:hover .card__image {
    transform: scale(1.2);
  }
  .card__content {
    position: relative;
    z-index: 1;
    padding: 20px 20px 5px;
    height: 100%;
    background: #fff;
    transition: margin-top .3s;
  }
  .card:hover .card__content {
    margin-top: -66px;
  }
  .card__title {
    margin: 0 0 5px;
    font-size: 18px;
    font-weight: 500;
    color: hsl(var(--ui-primary));
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .card__text {
    color: hsl(var(--ui-primary));
    font-size: 14px;
    font-weight: 400;
    margin: 20px 0;
    overflow: hidden;
    height: 0;
    line-height: 22px;
    margin: 0;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    transform-origin: 0 0;
    transition: height .3s;
  }
  .card:hover .card__text {
    height: 66px;
  }
  .card__tags {
    font-size: 12px;
  }
  .card__tags .card__icon {
    width: 16px;
  }
  .card__footer {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .card__date {
    flex: 1;
    padding: 15px 0 15px 20px;
    font-size: 13px;
    cursor: pointer;
    color: hsl(var(--ui-primary-h), var(--ui-primary-s), calc(var(--ui-primary-l) + 20%));
  }
  .card__like {
    width: 18px;
    margin-right: 12px;
  }

  .not-result {
    padding: 150px 0;
    text-align: center;
    color: hsl(var(--ui-primary-h), var(--ui-primary-s), 70%);
    font-size: 20px;
  }

  @media (max-width: 900px) {
    .grid {
      grid-gap: 20px;
    }
  }

  @media (max-width: 500px) {
    .grid {
      grid-template-columns: 1fr;
    }
  }
</style>